import * as React from "react"

type buttonState =
{
    state : number 
}

var globalState : buttonState =
{
    state : 0
}

function switchState(btnState : number) : buttonState
{
    var currentState = btnState

    if(currentState = 0)
        currentState = 1 
    else if(currentState == 1)
        currentState = 2
    
    return {state : currentState}
}

function updateButtonState() : void
{
    var newState = switchState(globalState.state)
    globalState.state  = newState.state 
}

function FancyButton({name : string})
{
    var i = 0
    return(
        <body>
        <button onClick={() => i++}>FancyButtonName</button>
        <p>{i}</p>
        </body>
    )
}

export default FancyButton
